.CreateTodoButton{
    background-color: transparent;
    border: none;
    cursor: pointer;
    color: rgb(48, 131, 187);
    font-size: 90px;
    position: fixed;
    bottom: 48px;
    right: 48px;
    font-weight: bolder;
    text-shadow: 0px 0px 8px rgb(28, 149, 230);

    transform: rotate(0);
    transition: all .06s linear;
}
.CreateTodoButton:focus{
    transform: rotate(45deg);
}